.main_page{
    width: 500px;
}

.main_page .change_language{
    font-family: "Eulyoo - SemiBold";
    font-size: 15pt;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    padding-top: 30px;
}

.main_page .change_language > button{
    display: flex;
    flex-direction: row;
}

.main_page .change_language .button_change{
    background: url('../svg/Change.svg') no-repeat;
    width: 30px;
    height: 30px;
}

.main_page .change_language div{
    color: white;
    font-family: "Eulyoo - SemiBold";
    font-size: 15pt;
}

.main_page .main_page_header_logo{
    width: 300px;
    height: 100px;
}

.main_page .main_title{
    font-family: "Eulyoo - SemiBold";
    font-size: 25pt;
    height: 40%;
    min-height: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.main_page .button_start{
    width: 300px;
    height: 100px;
    color: white;
    font-size: 17pt;
    background: var(--main-red);
    border-radius: 10px;
    font-family: "NanumSquare - Bold";
    margin: 70px 0 70px 0;
}

.main_page .main_enquiry_information{
    font-family: "NanumBarunGothic - Light";
    padding-bottom: 100px;
    line-height: 1.7;
}
